import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import './signup.css';
import './signin.css';
class Signup extends Component {
    constructor(props){
        super(props)
        this.state={
        firstName:"",
        lastName:"",
        phone:"",
        adress:"",
        email:"",
        password:"",

        }
    }
    signupnom=(e)=>{
        if(e.target.value ===""){
        alert(" Please write your first name!!! ")
        }else{
        this.setState({
            firstName:e.target.value  
            }) }
    }
    signupprenom=(e)=>{
     if(e.target.value ===""){
        alert("Please write your last name")
     }else{
     this.setState({
        lastName:e.target.value    
         })}
    }
    signupnumber=(e)=>{
        if(e.target.value ===""){
         alert("Please write your number!!!")
        }else{
        this.setState({
            phone:e.target.value
            })}
    }
    signupadress=(e)=>{
        if(e.target.value ===""){
       alert("Please write your adress!!")
        }else{
        this.setState({
            adress:e.target.value
            })}
    }
    signupmail=(e)=>{
    if(e.target.value ===""){
        alert("Please write your email!!!")
    }else{
    this.setState({
        email:e.target.value  
        })}
    }
    signuppassword=(e)=>{
        if(e.target.value ===""){
        alert("Please write your password!!!")
        }else{
            this.setState({
                password:e.target.value 
                })
        }
    }
    onAddUser=()=>{
    axios.post("http://localhost:4000/users",{
        firstName:this.state.firstName,
        lastName:this.state.lastName,
        phone:this.state.phone,
        adress:this.state.adress,
        email:this.state. email,
        password:this.state.password,
    })
    .then(res=> console.log(res))
    .catch(err => console.log(err))
    }
    render() { 
        return ( 
            <div className="section-singin">
                <h1 className="signin-title">Sing up</h1>
                <form action="" className="sign-form">
                    <input type="text" placeholder=" Your First Name..." onChange={this.signupnom} className="signin-input" />
                    <input type="text" placeholder=" Your Last Name..." onChange={this.signupprenom}  className="signin-input" />
                    <input type="text" placeholder="Your Phone Number..." onChange={this.signupnumber} className="signin-input" />
                    <input type="text" placeholder="Your Adress..." onChange={this.signupadress} className="signin-input" />
                    <input type="email" placeholder="Your email..." onChange={this.signupmail} className="signin-input" />
                    <input type="password" placeholder="Your Password..." onChange={this.signuppassword} className="signin-input" />
                    <div className="sign-redirect">
                        <span className="sign-redirect-text">Already have an account? Click here to </span>
                        <Link className="sign-redirect-link" to="/Signin">Sign in</Link>
                    </div>
                    <input type="submit" onClick={this.onAddUser} value="Sign up" className="sign-submit" />
                </form>
            </div>
         );
    }
}
 
export default Signup;