import React, { Component } from 'react';
import './portfolio.css';
import axios from 'axios'
class Portfolio extends Component {
    state = { 
        portfolio:[]
     }
     componentDidMount(){
        axios.get('js/data.json').then(res =>{this.setState({portfolio:res.data.portfolio})})
    }
    render() {
        const {portfolio}=this.state; 
        const portfolioLists = portfolio.map((portfolioItem)=>{
            return(
                <div className="boxdiv" key={portfolioItem.id}>
                    <img className="img-box" src={portfolioItem.image} alt=""/>
                    <p class="over">
                        
                    </p>
                </div>
            )
        })

        return ( 
            <div className="divs portfolio" id="portfoliopage">
                <div className="container">
                <div className="div-header">
                <h2 className="div-tittle">Our Gallery</h2>
                <div className="line">
                    <span></span>
                </div>
                <p>We try to share our photos with our loyal customers,where he can share our services</p>
                </div>
                </div>
                <div className="ul-portfolio">
                <ul class="portfolio-list">
                <li class="portfolio-item active">All</li>
                
            </ul>
                </div>
                <div className="box">
                {portfolioLists}
                </div>
            </div>
         );
    }
}
 
export default Portfolio;