import React, { Component } from 'react';
import './signin.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
class Signin extends Component {
   constructor(props){
       super(props)
       this.state={
        mail: "",
        password: "",
        user: {}
       }
   }
   signinemail=(e)=>{
       if(e.target.value ===""){
        alert("Please write your email")
       }else{
           this.setState({
              mail:e.target.value 
           })
         
       }
   }
   signinpassword=(e)=>{
       if(e.target.value ===""){
           alert("Please write your password")
       }else{
           this.setState({
            password:e.target.value
           })
       }
   }
   redirection=(e)=>{
    e.preventDefault();
    const { history } = this.props;
this.props.history.push('/Articles')
   axios.post(`http://localhost:4000/login/${this.state.mail}/${this.state.password}`)
  .then(res=> {
    if(res.data.length>0){
        this.setState({
         user: res.data
        })
        history.push('/Articles')  
    }
    else { alert("Valid Compte.")}
  })
  .catch(err => console.log("err"));
   }
    render() { 
        return ( 
            <div className="section-singin">
                <h1 className="signin-title">Sing in</h1> 
                <form onSubmit={(e) => this.redirection(e)} action="" className="signin-form">
                    <input type="email" placeholder="Enter Your E-mail..." onChange={this.signinemail} className="signin-input"/>
                    <br/>
                    <input type="password" placeholder="Enter Your Password..." onChange={this.signinpassword} className="signin-input"/>
                    <div className="sign-redirect">
                        <span className="sign-redirect-text"> If you don't have an account, Click here to </span>
                        <Link className="sign-redirect-link" to="/Signup">Sign up</Link>
                        <br/>
                        <input type="submit" value="Log in" className="sign-submit" />
                    </div>
                </form>
            </div>
         );
    }
}
 
export default Signin;