import React, { Component } from 'react';
import './services.css'
import axios from 'axios'
class Services extends Component {
    state = { 
        works:[]
     }
componentDidMount(){
    axios.get('js/data.json').then(res =>{this.setState({works:res.data.works})})
}

    render() { 
      const {works}=this.state;
      const worksLists = works.map((workItem)=>{
          return (
            
                <div className="services-card first" key={workItem.id}>
                <i className={workItem.icon_name}></i>
                <h4 className={workItem.title}>{workItem.title}</h4>
                <hr className="line"/>
                <p className="part-desc">
                    {workItem.body}
                </p>
                </div>

           
          )
      } )

        return ( 
            <div className="divs services" id="servicespage">
                <div className="container">
                <div className="div-header">
                <h2 className="div-tittle">Our Services</h2>
                <div className="line">
                    <span></span>
                </div>
                <p>We provide the best services for our customers</p>
                </div>
                </div>
            <div className="services1">
                <div className="container">
                    {worksLists}

                </div>

            </div>

                
            </div>
         );
    }
}
 
export default Services;