import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import './navbar.css';
import Router from '../router/router.js';
import $ from 'jquery';

class Navbar extends Component {
    state = {  }

    componentDidMount(){
      $(document).ready(function(){
       $(window).scroll(function(){
        var sc = $(this).scrollTop();
        if(sc > 50){  
            $('.navbar').addClass('.sticky');
        }
        else {
            $('.navbar').removeClass('.sticky');
        }

       })
      })  
    }

    render() { 
        return ( 
            //Navbar
            <div className="navbar">
             <div className="container">
                <div className="logo">
                <h2 className="h2-logo">Delivery.tn</h2>
                </div> 
               <ul className="ul-list">
               <li className="list-item"><Link className="a-li" to="/">Home</Link></li>
                <li className="list-item"><a className="a-li" href="#aboutpage">About</a></li>
                <li className="list-item"><a className="a-li" href="#servicespage">Services</a></li>
                <li className="list-item"><a className="a-li" href="#portfoliopage">Gallery</a></li>
                <li className="list-item"><a className="a-li" href="#fieldpage">Field</a></li>
                <li className="list-item"><a className="a-li" href="#teampage">Team</a></li>
                <li className="list-item"><a className="a-li" href="#contactpage">Contact</a></li>
                <button className="btn-articles"> <li className="list-item"><Link className="a-li" to="/Articles">Our Food</Link></li></button>
               </ul>

             </div>
               
            </div>
         );
    }
}
 
export default Navbar;