import React, { Component } from 'react';
import './contact.css';
import axios from 'axios'
class Contact extends Component {
    constructor(props){
        super(props)
        this.state=({
            firstnamecontact:'',
            lastnamecontact:'',
            numbercontact:'',
            emailcontact:'',
            messagecontact:''
        })
    }
    contactfirstname=(e)=>{
        this.setState({
            firstnamecontact:e.target.value   
        })
    }
    contactlastname=(e)=>{
        this.setState({
            lastnamecontact:e.target.value  
        })
    }
    contactnumber=(e)=>{
        this.setState({
        numbercontact:e.target.value  
        })
    }
    contactemail=(e)=>{
    this.setState({
    emailcontact:e.target.value
    })
    }
    contactmessage=(e)=>{
        this.setState({
        messagecontact:e.target.value
        })
    }
    addcontact= () =>{
        axios.post("http://localhost:4000/contacts", {
      
            firstnamecontact: this.state.firstnamecontact,
            lastnamecontact:this.state.lastnamecontact,
            numbercontact:this.state.numbercontact,
            emailcontact:this.state. emailcontact,
            messagecontact:this.state. messagecontact
          })
          .then(res=> console.log(res))
          .catch(err => console.log(err))
    }
    render() { 
        return ( 
            <div className="divs contact" id="contactpage">
                <div className="container">
                <div className="div-header">
                <h2 className="div-tittle">Contact Us</h2>
                <div className="line">
                    <span></span>
                </div>
                <p> We share with our customers our contact, where they can contact us</p>
                </div>
                </div>
                <div className="div-contact">
                 <div className="div1-contact">
                 <h3 class="h3-div1-contact">Have You Any Qestions ?</h3>
                <span className="s1-div1-contact"><i class="fa fa-map-marker"></i>39 Rue de Habib Maazoun, Sfax 3000</span>
                <span className="s2-div1-contact"><i class="fa fa-phone"></i>74 555 555</span>
                <span className="s3-div1-contact"><i class="fa fa-envelope"></i>Delivery.tn@gmail.com</span>
                <span className="s4-div1-contact"><i class="fa fa-globe"></i>www.Deliverytn.com</span>
                 </div>
                 <div className="div2-contact">
                  <form action="">
                  <div className="form-group">
                    <input onChange={this.contactfirstname} className="input" type="text" placeholder="Your First Name........" required/>
                    <input onChange={this.contactlastname} className="input" type="text" placeholder="Your Last Name........" required/>
                  </div>
                  <div className="form-group">
                    <input onChange={this.contactnumber} className="input" type="text" placeholder="Your Phone Number........" required/>
                    <input onChange={this.contactemail} className="input" type="email" placeholder="Your email........" required/>
                   </div>
                   <textarea onChange={this.contactmessage} className="textarea" name="" placeholder=" Your Message........" id="" cols="30" rows="10" required></textarea>
                   <br/>
                   <button className="form-btn" onClick={this.addcontact}>Send Message</button>
                  </form>
                 </div>
                </div>
            </div>
         );
    }
}
 
export default Contact;