import React, { Component } from 'react';
import './team.css';
import axios from 'axios'
class Team extends Component {
    state = { 
        team:[]
     }
componentDidMount(){
    axios.get('js/data.json').then(res=>{this.setState({team:res.data.team})})
}


    render() { 
        const {team}=this.state;
        const teamLists = team.map((teamItem)=>{
            return(
                <div className="div1-teaminfo" key={teamItem.id}>
                     <img className="img-div1-teaminfo" src={teamItem.photo}/>
                     <h4 className="h4-div1-teaminfo">{teamItem.name}</h4>
                     <span className="s-div1-teaminfo">{teamItem.description} </span>
                     <p className="p-div1-teaminfo">
                     <i className="fa fa-facebook" ></i>
                     <i className="fa fa-twitter"></i>
                     <i className="fa fa-youtube"></i>
                     <i className="fa fa-linkedin"></i>
                     <i className="fa fa-google-plus"></i>
                     <span className="bg"></span>
                     </p>
                     
                   </div>
            )
        })
        return ( 
            <div className="divs team" id="teampage">
                <div className="container">
                <div className="div-header">
                <h2 className="div-tittle">Our Team</h2>
                <div className="line">
                    <span></span>
                </div>
                <p>We share with our customers informations about our team, where they can contact them</p>
                </div>
                </div>
               <div className="teaminfo">
                   {teamLists}

               </div>

            </div>
         );
    }
}
 
export default Team;